.landing {
  position: relative;
  height: 100vh;
  background-color: #033043;
  background-image: linear-gradient(45deg, #27404fec 0%, #09232bed 74%),
    url(/static/media/landingBg.bd60aa1b.jpg);
  background-size: cover;
  background-position: bottom;
  background-repeat: no-repeat;
  background-attachment: fixed;
}

#particles {
  position: absolute;
  width: 100%;
  height: 100%;
}

.landing__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 5;
}

.landing__sub-1 {
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: flex-start;
  align-items: center;
  height: 100%;
  padding: 100px 0;
  margin-left: 70px;
  /* margin-top: 170px; */
}

/* .landing__sub-2{

} */

.landing__heading {
  font-size: 120px;
  font-family: 'Archive';
  font-display: swap;
  -webkit-margin-before: 0;
          margin-block-start: 0;
  -webkit-margin-after: 0;
          margin-block-end: 0;
  margin: 0 20px;
  animation-name: moveInTop;
  animation-duration: 1s;
  color: #fff;
}

.landing__sub-heading {
  font-family: 'Alegreya Sans', sans-serif;
  /* font-style: italic; */
  font-size: 42px;
  -webkit-margin-before: 0;
          margin-block-start: 0;
  -webkit-margin-after: 0;
          margin-block-end: 0;
  animation-name: moveInLeft;
  animation-duration: 1s;
  color: #ddd;
}

.landing__sub-heading-2 {
  font-family: 'Alegreya Sans', sans-serif;
  font-size: 25px;
  margin: 10px;
  color: #ddd;
}

.green {
  color: #10aa62;
}

.landing__btn {
  height: 50px;
  width: 150px;
  position: relative;
  z-index: 2;
  padding: 10px;
  margin: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #23b574;
  border: none;
  outline: none;
  border-radius: 14px;
  box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.377);
  color: #000;
  font-size: 20px;
  font-family: 'Alegreya Sans', sans-serif;
  cursor: pointer;
  transition: all 0.3s ease;
}

.landing__btn:hover {
  box-shadow: 5px 5px 20px rgba(0, 0, 0, 0.377);
  /* transform: translateY(-5px); */
  background-color: #2ea56f;
  scale: 1.06;
}

.hex__back-1 {
  position: absolute;
  top: 12%;
  right: 19%;
}

.hex__back-2 {
  position: absolute;
  top: 10%;
  right: 18.3%;
}

a {
  text-decoration: none;
}

.landing__sub-heading strong {
  font-weight: 700;
}

.landing__logo {
  height: 350px;
  position: absolute;
  top: 15%;
  right: 19%;
  /* align-self: flex-end; */
  /* margin: 80px; */
  /* margin-right: 300px; */
}

.svg-wave {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  line-height: 0;
  overflow: hidden;
  transform: rotate(180deg);
}

.svg-wave svg {
  position: relative;
  display: block;
  width: calc(100% + 1.3px);
  height: 150px;
}

.svg-wave .shape-fill {
  fill: #ffffff;
}

.custom-shape-divider-bottom-1618824420 {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  overflow: hidden;
  line-height: 0;
  transform: rotateY(180deg);
}

.custom-shape-divider-bottom-1618824420 svg {
  position: relative;
  display: block;
  width: calc(111% + 1.3px);
  height: 225px;
}

.custom-shape-divider-bottom-1618824420 .shape-fill {
  fill: #ffffff;
}

