.backdrop {
  margin: 10px;
  margin-top: 40px;
  margin-left: 20px;
  width: 85%;
  height: 250px;
  border-radius: 20px;
  padding: 10px;
  background: #fff;
  z-index: 2;
  transform-style: preserve-3d;
  transform: perspective(1000px);
  transition: all 0.3s ease;
}

.backdrop:hover {
  transform: scale(1.07);
}

.over-front {
  height: 100%;
}

.text {
