/* @import url('https://fonts.googleapis.com/css2?family=Cinzel:wght@400;600;900&display=swap'); */
@font-face {
  font-family: 'Archive';
  src: url(/static/media/Archive-Regular.27d1e320.ttf);
  font-weight: normal;
  font-style: normal;
}

body {
  margin: 0;
  padding: 0;
}

.archive {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(210px, 1fr));
  grid-gap: 32px;
  grid-auto-flow: dense;
}

/* Extra-wide grid-posts */
.archive:nth-child(31n + 1) {
  grid-column: 1 / -1;
}
.archive:nth-child(16n + 2) {
  grid-column: -3 / -1;
}
.archive:nth-child(16n + 10) {
  grid-column: 1 / -2;
}

/* Single column display for phones */
@media (max-width: 459px) {
  .archive {
    display: flex;
    flex-direction: column;
  }

  body {
    overflow-x: unset;
  }
}

