#apply-button {
  position: absolute !important;
  z-index: 5 !important;
  background-color: #22b573 !important;
  cursor: pointer;
  transition: all 0.3s ease !important;
}

#apply-button:hover {
  background-color: #1c965f !important;
  transform: scale(1.04);
}

.btn_telegram {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  background-color: #0084c6;
  outline: none;
  border: none;
  cursor: pointer;
  width: 312px;
  border-radius: 4px;
  padding: 0 30px;
  height: 44px;
  font-size: 20px;
  color: #fff;
  position: relative;
  z-index: 5;
  font-family: Nunito Sans, sans-serif;
  transition: all 0.3s ease !important;
  top: 90px;
}

.btn_telegram div {
  position: relative;
  top: 2px;
  margin-left: 5px;
}

.btn_telegram:hover {
  transform: scale(1.04);
  background-color: #0075af;
}

